import axios from 'axios';
var baseUrl="https://www.easy-mock.com/mock/5c2dc0ebda4d71382bd2d0ce/";
class HTTP {
//params={}为url后面跟的参数 start、count ...
request({url,method="get",data={},params={}}){
    return axios({
        url:baseUrl+url,
        method,
        data,
        params
    })
}

}
export {HTTP};