<template>
    <div class="wrapper">
        <swiper :options="swiperOption">
            
            <swiper-slide>
                <div class="content">
                     <div class="conter"  v-for="item of icon" :key="item.id">
                     <img class="swiper-img" :src="item.imgUrl" >
                <a>{{item.title}}</a>
                </div>
                    </div>    
            </swiper-slide>
            <swiper-slide>
             <div class="content">
                  <div class="conter"  v-for="item of icons" :key="item.id">
                     <img class="swiper-img" :src="item.imgUrl" >
                <a>{{item.title}}</a>
                </div>
             </div>
            </swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>

</template>
<script>
export default {
  name: "NavSwiper",
  props: {
    icons: {
      type: Array
    },
    icon: {
      type: Array
    }
  },
  data() {
    return {
      swiperOption: {
        //是否有圆点
        pagination: {
          el: ".swiper-pagination",
          type: "bullets"
        }
      }
    };
  }
};
</script>
<style scoped>
.swiper-img {
  width: 150px;
  height: 150px;
}
.wrapper >>> .swiper-pagination-bullet-active {
  background: rgb(75, 2, 247) !important;
}
.content {
  display: grid;
  grid-template-rows: repeat(2, 200px);
  grid-template-columns: repeat(4, 1fr);
}
.content p {
  font-size: 22px;
}
.conter {
  justify-self: center;
  align-self: center;
  text-align: center;
  display: flex;
  flex-direction: column;
}
</style>