<template>
<div class="wrapper" ref="wrapper">
      <div class="s-content">
        <div class="area">
            <p class="title">当前城市</p>
            <div class="content">
                <div class="btn">
                    <!-- 使用vuex中的数据 -->
                    {{this.$store.state.city}}
                </div>
            </div>
        </div>

        <div class="area">
            <p class="title">热门城市</p>
            <div class="content">
                <div class="btn" v-for="(item,index) of hotCities" :key="index">
                    <div @click="handleclick(item.name)"> {{item.name}}</div>
                  
                </div>
            </div>
        </div>
        <div class="area" v-for="(item,index) in cities" :key="item.id" :ref="index">
            <p class="title">{{index}}</p>
            <div class="list-city" @click="handleclick(index.name)" v-for="(index,inx) of item" :key="inx">{{index.name}}</div>
        </div>
    </div>
</div>
  
</template>
<script>
import BScroll from "better-scroll";
export default {
    name:"CityList",
    props:['hotCities','cities'],
    methods:{
      handleclick(city){
           this.$store.commit("changeCity",city);
           this.$router.push("/");
      }
  },
     mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
//通过计算属性接收到子组件（word）传递给vuex然后传过来的参数letter
  computed: {
      //通过定义参数的函数来获取到参数
      letter(){
          return this.$store.state.letter
      }
  },
  watch:{
      //通过定义参数的监听函数监听参数的改变从而改变html中设置 :ref="index"属性的值
      //:ref="index"为页面所跳转的位置里面传的参数为for循环的对象中的属性名相当于数组中的下标因为循环的是个对象
      letter(){
          //console.log(1)
        //   console.log(this.$refs)
          //控制页面跳转到本页面中的指定位置
          const element = this.$refs[this.letter][0];
          this.scroll.scrollToElement(element);
      }
  },
  
}
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  height:1000px;
  overflow: hidden;
  position: relative;
  margin-top: 176px;
}

.title{
    padding-left:20px;
    line-height: 60px;
    background: #eee;
}
.btn{
    width:200px;
    height:54px;
    border-radius: 10px;
    border:1px solid #ccc;
    text-align: center;
    line-height: 54px;
}
.content{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    padding:20px;
    grid-row-gap: 20px;
}
.list-city{
    padding-left:20px;
    line-height: 70px;
    border-bottom: 1px solid #ccc;
}
</style>
