import {HTTP} from "./HTTP";
class HomeModels extends HTTP {
    getlist(city){
       return this.request({
            url:"vueProject/project",
            params:{
                city
            }
        })
    }
    getdetail(id){
       return this.request({
            url:"vueProject/detail/",
            params:{
                id
            }
        })
    }
    getcity(){
       return this.request({
            url:"vueProject/city",
        })
    }
}
export {HomeModels};