<template>
  <div class="home">
    <div class="bomebody" v-if="isloading">
   <home-head></home-head>
     <home-swiper :swiperlist="swiperList" ></home-swiper>
     <nav-swiper :icons="icons" :icon="icon"></nav-swiper>
     <div class="hot">
       <div class="hotTop">
         <p>热销推荐</p>     
       </div>
        <div v-for="item of recommendList" :key="item.id">
           <hot-item :recommendList="item"></hot-item>
         </div>
      </div>
     <div class="where">
       <div class="hotTop">
         <p>周末去哪儿</p>     
       </div>
        <div v-for="item of weekendList" :key="item.id">
           <where-item :weekendList="item"></where-item>
         </div>
      </div>
    </div>
  <div class="loding" v-if="!isloading">
    <Loading></Loading>
  </div>
  </div>
</template>

<script>
import HomeHead from "./components/Head";
import HomeSwiper from "./components/Swiper";
import NavSwiper from "./components/Nav";
import HotItem from "./components/Hot";
import WhereItem from "./components/Where";
import Loading from "../../components/loading";
// import Loading from "@/components/loading"; 和上面效果相同
import { HomeModels } from "../../models/HomeModel";
const homeModel = new HomeModels();

export default {
  name: "home",
  components: {
    HomeHead,
    HomeSwiper,
    NavSwiper,
    HotItem,
    WhereItem,
    Loading
  },
  data() {
    return {
      swiperList: [],
      icon: [],
      icons: [],
      recommendList:[],
      weekendList:[],
      currentCity:"",
      isloading:false
    };
  },
  computed:{
  city(){
  return this.$store.state.city
    }
  },
 // mounted(){}也可以
  created() {
    this.currentCity=this.city
    homeModel.getlist(this.city).then(res => {
      var swiperList = res.data.gallary;
      var temp = res.data.icons;
      var recommendList = res.data.recommendList;
      var weekendList = res.data.weekendList;
      var icon = temp.slice(0, 8);
      var icons = temp.slice(8, 10);
      this.swiperList = swiperList;
      this.icons = icons;
      this.icon = icon;
      this.recommendList = recommendList;
      this.weekendList = weekendList;
      this.isloading=true
    });
   
  },
  //如果页面设置了keep-alive缓存则每次进入页面都会触发的函数
  // （因为主页面设置了放置缓存不会发送请求（当city变化时要发送请求取主页面相应数据））
  activated() {
    if(this.currentCity!=this.city){
      this.currentCity=this.city;
      homeModel.getlist(this.city).then(res => {
      var swiperList = res.data.gallary;
      var temp = res.data.icons;
      var recommendList = res.data.recommendList;
      var weekendList = res.data.weekendList;
      var icon = temp.slice(0, 8);
      var icons = temp.slice(8, 10);
      this.swiperList = swiperList;
      this.icons = icons;
      this.icon = icon;
      this.recommendList = recommendList;
      this.weekendList = weekendList;
    });
    }
  },
  //另一个方法使用监听属性监听值得变化(created函数中不需要this.currentCity=this.city)
  // watch:{
  //   city(){
  //      homeModel.getlist(this.city).then(res => {
  //     var swiperList = res.data.gallary;
  //     var temp = res.data.icons;
  //     var recommendList = res.data.recommendList;
  //     var weekendList = res.data.weekendList;
  //     var icon = temp.slice(0, 8);
  //     var icons = temp.slice(8, 10);
  //     this.swiperList = swiperList;
  //     this.icons = icons;
  //     this.icon = icon;
  //     this.recommendList = recommendList;
  //     this.weekendList = weekendList;
  //   });
  //   }
  // },
};
</script>
