import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import mutations from './mutations'

Vue.use(Vuex)

export default new Vuex.Store({
  //相当于data
  state,
  mutations,
  //接收子组件传递的事件1 将改变的值传给mutations
  actions: {
    changeLetter(ctx,letter){
      // console.log(letter);
      // 通过commit传递
      ctx.commit("changeLetter",letter)
    }
  }
})
