<template>

        <div class="wrapper" @click="handleclick">
        <swiper :options="swiperOption">  
            <swiper-slide v-for="item of swiperList" :key="item.id">
                <img class="swiper-img" :src="item.imgUrl" >
            </swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination" slot="pagination"></div>
            <div class="swiper-scrollbar" slot="scrollbar"></div>
        </swiper>
    </div>
</template>
<script>
export default {
  name: "DetailSwiper",
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".swiper-pagination",
          type: 'fraction',
        },
        /* 是否循环 */
        loop: true,
        autoplay: false,
        //当组件套组件的时候会出现一些错误，设置两个等于true
        observer: true,
        observerParents:true
      },
    };

  },
    props: {
    swiperList: {
      type: Array
    }
  },
  methods:{
      handleclick(){
          this.$emit("toggle")
      }
  }
};
</script>
<style lang="scss" scoped>
.wrapper {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: #333;
  z-index: 1;
}
.swiper-img {
  width: 100%;
  height: 300px;
}
.swiper-container {
  top: 50%;
  transform: translateY(-50%);
}
.swiper-pagination {
  bottom: -50px;
  color: #fff;
}
.wrapper >>> .swiper-container {
  overflow: inherit;
}
</style>