function getCity(){
    var defaultCity="新疆";
    try {
      if(localStorage.getItem("city")){
        defaultCity=localStorage.getItem("city")
      }
    } catch (error) {
      console.log(error)
    }
    return defaultCity;
  }
export default{
    letter:"",
    //取缓存
    city:getCity()
    // city:""
}