<template>
    <div class="detail">
      <div class="nav" :style="isOpacity" >
        <div iv v-show="isShowNav">
          <span class="iconfont back">&#xe606;</span>
          <span class="title">{{detailList.sightName}}</span>
        </div>
      </div>
      <router-link to="/">
         <div class="ico" v-show="!isShowNav"> <span class="iconfont">&#xe606;</span></div>
      </router-link>
       
      <div class="top" @click="handleClick">
        <img :src="detailList.bannerImg" alt="">
      </div>
      <!-- 图片轮播 -->
      <VFade>
        <detail-swiper slot="fade" @toggle="hide" v-if="isshow" :swiperList="detailList.gallaryImgs"></detail-swiper>
      </VFade>
      <!-- 门票 -->
      <list :listitem="detailList.categoryList"></list>
    </div>
</template>
<script>
import { HomeModels } from "../../models/HomeModel";
import DetailSwiper from "./components/DetailSwiper";
import VFade from "./components/Fade";
import list from "./components/List";
const homeModel = new HomeModels();
export default {
    name: "details",
  data() {
    return {
       detailList:{},
       isshow:false,
       isnav:false,
        isOpacity:{
        opacity:0
        },
       isShowNav:false
    };
  },
  components:{
    DetailSwiper,
    VFade,
    list
  },
   created() {
    let id=this.$route.params.id
    homeModel.getdetail(id).then(res=>{
     this.detailList=res.data.data
    })
   },
     mounted() {
    window.addEventListener('scroll',this.handleScroll)
  },
   methods:{
     handleClick(){
       this.isshow=true;
     },
     hide(){
       this.isshow=false;
     },
       handleScroll(){
      var top =  document.documentElement.scrollTop 
      || document.body.scrollTop;
      /* 滚动条距离顶部的距离达到200时候,opacity==1 */
          if (top > 0) {
        let opacity = top / 200;
        opacity = opacity > 1 ? 1 : opacity;
        this.isOpacity.opacity = opacity;
        this.isShowNav = true;
      } else {
        this.isShowNav = false;
      }
   },
     destroyed(){
    window.removeEventListener("scroll",this.handleScroll);
  }
   },
}
</script>
<style lang="scss" scoped>
.detail{
  height: 1500px;
}
.top img{
  width: 750px;
  height: 400px;
}
.ico span{
  color: rgb(156, 151, 151);
  font-size: 45px;
}
.back{
  color: #fff;
  font-size: 45px;
  position: absolute;
  left: 20px;
  top: 15px;
}
.ico{
  background:rgb(194, 192, 192) ;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  left: 20px;
}
.nav{
  position: fixed;
  top: 0;
  width: 100%;
  height:80px;
  background: #00BCD4;
  display: flex;
  align-items: center;
  justify-content: center;
}
.title{
  color: #fff;
  font-size: 30px;
}
</style>
