<template>
    <div class="content">
        <input v-model="keyword"  type="text" placeholder="输入你要搜索的城市"/>
        <div class="searchconter" v-show="keyword">
            <div class="wrapper" ref="wrapper">
                 <ul class="searchitem">
                <li @click="handleclick(item.name)" v-for="item in list" :key="item.id">
                  {{ item.name }}
               </li>
            </ul>
            </div>
            <div v-show="!this.list.length" class="no"><p>没有搜索到你想要的城市</p></div>
        </div>
       
    </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
    name:"CitySearch",
    methods:{
handleclick(city){
    // console.log(city)
     this.$store.commit("changeCity",city);
      this.$router.push("/");
}
    },
     mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
    data() {
        return {
            keyword:"",
            //接收能够搜索到数据
            list:[]
        }
    },
    watch:{
        //双向数据绑定 同过输入框中输入的数匹配相应的项
   keyword(){
       console.log(1)
    if(!this.keyword){
        this.list=[];
        return;
    }
    var result=[]
    // 循环对象用for-in
    for (var i in this.cities) {
        // console.log(this.cities)
       this.cities[i].forEach(ele => {
           if(ele.spell.startsWith(this.keyword) || ele.name.startsWith(this.keyword)){
               result.push(ele)
           } 
       });
    }
     this.list = result;
   }
    },
    props:['cities']
}
</script>

<style lang="scss" scoped>
@import 'style/global.scss';
.content{
    position:fixed;
    width:100%;
    top: 90px;
    height:88px;
    display: flex;
    background: color(bg);
    align-items: center;
    justify-content: center;
    z-index: 99;
    input{
        border:1px solid #eee;
        border-radius: 15px;
        outline:none;
        font-size: 14px;
        padding-left:20px;
        height:66px;
        width:92%;
    }
}
.searchconter{
    position: absolute;
    top: 90px;
    background: #fff;
    height: 1000px;
    width: 100%;
}
.searchitem li{
   line-height: 62px;
   padding-left: 20px;
   background: #fff;
   border-bottom: 1px solid #eee;
}
.no{
    width: 100%;
    height: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.wrapper {
  width: 100%;
  height:1000px;
  overflow: hidden;
  position: relative;
}

</style>
